import React from "react";
import HeaderStatic from "@/app/components/home/HeaderStatic";
import Image from "next/image";
import Link from "next/link";
import { blogs } from "@/app/data/BlogData";
import RelatedBlogsCarousel from "@/app/components/blog/RelatedBlogsCarousel";

// Component to parse and render content with numbered headings
const BlogContent = ({ content }: { content: string }) => {
  const paragraphs = content.split("\n\n");

  return (
    <div className="space-y-6">
      {paragraphs.map((paragraph, index) => {
        // Check if paragraph starts with a number followed by a period
        const lines = paragraph.split("\n");
        const firstLine = lines[0];
        const match = firstLine.match(/^(\d+)\.\s+(.+)$/);

        if (match) {
          const number = match[1];
          const title = match[2];
          const body = lines.slice(1).join("\n").trim();

          return (
            <div key={index}>
              <h3 className="text-xl md:text-2xl font-semibold text-gray-900 mb-3">
                {number}. {title}
              </h3>
              {body && <p className="text-gray-700 leading-relaxed">{body}</p>}
            </div>
          );
        }

        return (
          <p
            key={index}
            className="text-gray-700 leading-relaxed whitespace-pre-line"
          >
            {paragraph}
          </p>
        );
      })}
    </div>
  );
};

async function BlogDetailPage({ params }: { params: Promise<{ id: string }> }) {
  const { id } = await params;
  const blogId = parseInt(id);
  const blog = blogs.find((b) => b.id === blogId);

  if (!blog) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-4xl font-bold mb-4">Blog not found</h1>
          <Link
            href="/blogs"
            className="px-6 py-3 bg-primary text-white rounded-lg"
          >
            Back to Blogs
          </Link>
        </div>
      </div>
    );
  }

  const relatedBlogs = blogs
    .filter((b) => b.id !== blog.id)
    .slice(0, 3)
    .map((b) => ({
      id: b.id,
      title: b.title,
      image: b.mainImage,
    }));

  return (
    <div>
      <HeaderStatic />

      {/* Breadcrumb */}
      <div className="container mx-auto px-6 md:px-16 ">
        <div className="flex justify-center mb-6">
          <div className="h-px w-full bg-black/30"></div>
        </div>

        <div className="flex items-center gap-2 text-sm text-gray-600">
          <Link href="/" className="hover:text-gray-900">
            Home
          </Link>
          <span>/</span>
          <Link href="/blogs" className="hover:text-gray-900">
            Blog
          </Link>
          <span>/</span>
          <span className="text-gray-900">{blog.breadcrumb}</span>
        </div>

        <div className="flex justify-center py-4">
          <div className="h-px w-full bg-black/30"></div>
        </div>
      </div>

      {/* Blog Content */}
      <article className="container mx-auto px-6 md:px-16 pb-0 sm:pb-16">
        <h1 className="text-4xl md:text-5xl/tight font-semibold text-black mb-8 ">
          {blog.title}
        </h1>

        {/* Main Image */}
        <div className="relative w-full h-[400px] md:h-[500px] rounded-2xl overflow-hidden mb-8">
          <Image
            src={blog.mainImage}
            alt={blog.title}
            fill
            className="object-cover"
          />
        </div>

        {/* Main Content */}
        <div className="prose prose-lg max-w-none mb-12">
          <p className="text-gray-700 leading-relaxed whitespace-pre-line">
            {blog.content}
          </p>
        </div>

        {/* Sections */}
        {blog.sections.map((section, index) => (
          <div key={index} className="mb-12">
            <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-6">
              {section.heading}
            </h2>

            {section.subheading && (
              <h3 className="text-2xl md:text-3xl font-semibold text-gray-800 mb-6">
                {section.subheading}
              </h3>
            )}

            <BlogContent content={section.content} />
          </div>
        ))}
      </article>

      {/* Related Blogs Carousel */}
      <RelatedBlogsCarousel relatedBlogs={relatedBlogs} />

      {/* Footer */}
      <div className="bg-primary relative xl:py-8 py-4 mt-10 md:m-0">
        <div className="absolute -top-[60px] xl:-top-[79px] w-full h-[80px] pb-10">
          <Image
            src="/teal-wave.png"
            alt="Wave"
            fill
            className="object-contain sm:object-fill"
          />
        </div>
        {/* <Footer /> */}
      </div>
    </div>
  );
}

export default BlogDetailPage;
