export const getBlogData = (id: string): Blog | undefined => {
  return blogs.find(blog => blog.id === parseInt(id));
};

export const getRelatedBlogs = (currentId: number) => {
  return blogs
    .filter(blog => blog.id !== currentId)
    .slice(0, 3)
    .map(blog => ({
      id: blog.id,
      title: blog.title,
      image: blog.mainImage
    }));
};